body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
    padding:50px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    font-size:14px;
}

.app_loading{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.movie{
    background-color:white;
    width:40%;
    display: flex;
    justify-content: space-between;
    align-items:flex-start;
    flex-wrap:wrap;
    margin-bottom:50px;
    text-overflow: ellipsis;
    padding:0 20px;
    box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133,0.22);
}

.movie_column{
    width:30%;
    box-sizing:border-box;
    text-overflow: ellipsis;
}

.movie_column:last-child{
    padding:20px 0;
    width:60%;
}

.movie h1{
    font-size:20px;
    font-weight: 600;
}

.movie .movie_genres{
    display: flex;
    flex-wrap:wrap;
    margin-bottom:20px;
}

.movie_genres .movie_genre{
    margin-right:10px;
    color:#B4B5BD;
}

.movie .movie_synopsis {
    text-overflow: ellipsis;
    color:#B4B5BD;
    overflow: hidden;
}

.movie .movie_poster{
    max-width: 100%;
    position: relative;
    top:-20px;
    box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80,80,80,0.22);
}

.app_loading{
    overflow:hidden;position:absolute;top:50%;left:50%;width:34px;height:28px;margin:-14px 0 0 -17px;background:url(http://pics.gmkt.kr/mobile/order/cart/coupon_loading.gif) no-repeat;background-size:28px auto;text-indent:-999px
}

@media screen and (min-width:320px) and (max-width:667px){
    .movie{
        width:100%;
    }
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait){
    .movie{
        width:100%;
        flex-direction: column;
    }
    .movie_poster{
        top:0;
        left:0;
        width:100%;
    }
    .movie_column{
        width:100%!important;
    }
}
